<script>

export default {
  name: 'ProjectsView',
  data: () => ({
    isRefactorName: false,
    nowName: '',
    projects: [
      {
        name: 'Lorem',
        id: '20301230120',
        img: null,
        lastActive: '16-10-2020'
      },
      {
        name: 'London',
        id: '203012323230120',
        img: 'https://i.dailymail.co.uk/i/newpix/2018/03/19/09/4A56F83800000578-0-image-a-2_1521452635493.jpg',
        lastActive: '16-9-2020'
      },
    ]
  }),
  computed: {},
  methods: {
    redactName({target}) {
      if (target.hasAttribute('readonly')) {
        target.removeAttribute('readonly')
        target.focus()
      }
    },
    setValue({target}) {
      target.textContent = this.nowName
    }
  },
  mounted() {

  },
}
</script>

<template>
  <section class="projects-view">
    <div class="container">
      <h3>
        <span class="rad">PROJECTS</span>
        <span class="main">PROJECTS</span>
        <span class="blue">PROJECTS</span>
      </h3>
      <div class="projects-bar">
        <router-link
            class="project-card"
            v-for="card in projects"
            :key="'prCard' + card.name"
            :to="'/projects/' + card.id"
            :style="{background: card.img
             ? `url(${card.img}) center/cover no-repeat`
             : '#ccc'}">
          <!-- need refactor to h1 -->
          <input @blur="setValue" @click="redactName" type="text" class="card-title" :value="card.name" readonly>
        </router-link>
      </div>
    </div>
  </section>
</template>


<style scoped lang="scss">

input {
  border: none;
  background: none;
  font: inherit;
}

.projects {
  &-bar {
    display: flex;
    flex-wrap: wrap;
  }

  &-view {
    padding: 2rem 0;
    flex: 1;
    background-color: #303030;
  }
}

.project-card {
  width: 300px;
  height: 13rem;
  color: #ffffff;

  margin-left: 2rem;

  position: relative;
  box-shadow: #ffffff -3px -3px 5px .3px, #222222 5px 5px 2px, #222222 10px 10px 5px;

  &:before {
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 4rem;
    background-color: #222222;
    clip-path: polygon(90% 33.34%, 100% 0, 0px 0px, 0% 75%);
  }
}

.card-title {
  color: #ffffff;
  position: relative;
  z-index: 3;
  top: 5px;
  left: 5px;
}

h3 {
  font-size: 2rem;
  text-align: center;
  margin: 1rem 0 3rem;
  font-family: "Glitch", sans-serif;

  position: relative;

  .main {
    position: relative;
    z-index: 2;
    text-shadow: #1e3799 -5px 5px 5px;
  }

  .rad {
    color: #b71540;
    position: absolute;
    bottom: 3px;
    left: 48%;
    opacity: .6;
    text-shadow: #b71540 4px 4px 6px;
    transform: translateX(-50%);
  }

  .blue {
    position: absolute;
    z-index: 1;
    top: 5px;
    left: 51%;
    transform: translateX(-50%);
    opacity: .8;
    color: #1e3799;
  }
}

</style>
